.header-container {
  // background-color: #ffb8ca;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 55;
}

.header {
  display: flex;
  align-items: center;
  @include for-size(mobile-only) {
    padding-top: 6px;
  }
  @include for-size(tablet) {
    padding-top: 37px;
  }
  @include for-size(desktop) {
    position: relative;
    padding-top: 37px;
    padding-bottom: 37px;
    justify-content: flex-end;
  }
}
.header-logo {
  margin-right: auto;
  @include for-size(desktop) {
    position: absolute;
    left: -6%;
  }
}
.header-burger-button {
  display: inline-flex;
  width: 40px;
  height: 40px;
  padding: 0;
  border: none;
  background-color: inherit;
  z-index: 21;

  @include for-size(desktop) {
    display: none;
  }
}

.header-burger-button-svg {
  width: 40px;
  height: 40px;
  transition: fill 250ms $timing-function;
}
.header-icon-close {
  display: none;
  fill: $white;
}
.header-burger-button.is-open .header-icon-close {
  display: block;
}
.header-burger-button.is-open .header-icon-menu {
  display: none;
}

.header-icon-menu {
  fill: $primary-accent;
}
.header-menu {
  display: none;
  right: 0px;
  top: 0;
  z-index: 20;

  @include for-size(mobile-only) {
    display: none;
    position: absolute;
    padding-top: 57px;
    height: 502px;
    background-color: #e17992;
    min-width: 250px;
    max-height: 502px;
    transition: 250ms;
    transition-timing-function: $timing-function;
  }

  @include for-size(tablet) {
    display: block;
    // transition: 250ms;
    transition-timing-function: $timing-function;
  }
  @include for-size(desktop) {
    display: flex;
    background-color: transparent;
    padding-top: 0px;
    flex-direction: row-reverse;
    align-items: center;
    position: relative;
  }
}
.header-menu.is-open {
  display: block;
  text-align: center;
  @include for-size(mobile) {
    z-index: 20;
  }
  @include for-size(tablet) {
    position: absolute;
    padding-top: 60px;

    background-color: #e17992;
    min-width: 254px;
    height: 1020px;
  }
  @include for-size(desktop) {
    display: flex;
    position: relative;
    min-width: auto;
    height: auto;
    background-color: transparent;
    padding-top: 0px;
    flex-direction: row-reverse;
    align-items: center;
  }
}
.header-menu.is-open .btn-header {
  margin-left: 0px;
  margin-top: 12px;
  margin-bottom: 19px;
  @include for-size(tablet) {
    // display: none;
    min-width: 186px;
  }
  @include for-size(desktop) {
    min-width: 159px;
    margin: 0px;
    margin-left: 110px;
  }
}
