.header-burger-button.is-open {
  position: absolute;
  top: 20px;
  right: 25px;
}
// .header-menu-button:hover,
// .header-menu-button:focus {
//   color 250ms $timing-function,
//     background-color 250ms $timing-function;
// }
.header-menu-button:hover .header-menu-svg,
.header-menu-button:focus .header-menu-svg {
  fill: currentColor;
  transition: fill 250ms $timing-function;
}
.header-menu-tablet {
  @include for-size(tablet) {
    display: none;
  }
  @include for-size(desktop) {
    display: flex;
  }
}
.header-menu.is-open .header-menu-tablet {
  text-align: left;

  @include for-size(tablet) {
    display: block;
  }
  @include for-size(desktop) {
    display: flex;
  }
}

.arrow-svg {
  width: 8px;
  height: 10px;
  fill: currentColor;
  margin-left: 12px;
}
.header-navigation {
  padding: 0px;
  margin: 0;
  list-style: none;
  @include for-size(desktop) {
    display: flex;
  }
}
.header-navigation-link {
  display: block;
  text-decoration: none;
  padding-top: 13px;
  padding-bottom: 13px;
  border: 1px solid #e18298;

  color: $white;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.3em;
  transition: color 250ms $timing-function;
  @include for-size(mobile-only) {
    padding-left: 20px;
    padding-right: 20px;
  }
  @include for-size(tablet) {
    padding-left: 24px;
  }
  @include for-size(desktop) {
    border: none;
    font-size: 16px;
    padding-left: 0px;
  }
  &:hover,
  &:focus {
    color: $primary-accent;
  }
}
.header-navigation-list:not(:last-child) {
  @include for-size(desktop) {
    margin-right: 35px;
  }
}
.header-pr-link {
  color: $primary-accent;
}

